<template>
    <div>
        <div ref="storeEcharts" :style="{ width: width, height: height }"></div>
    </div>
</template>

<script>
import * as echarts from "echarts";
export default {
    props: ['newOption'],
    data() {
        return {
            dataArr: [],
            width: "400px",
            height: "600px",
            myOption: {
                title: {
                    // text: 'Basic Radar Chart1'
                },
                legend: {
                    orient: 'vertical',
                    data: ['北京市', '大兴区'],
                    left: 'right',
                    textStyle: { //图例文字的样式
                        color: '#fff',
                        fontSize: 16
                    },
                },
                radar: {
                    splitArea: {
                        areaStyle: {
                        color: ['#77EADF', '#26C3BE', '#64AFE9', '#428BD4'],
                        shadowColor: 'rgba(0, 0, 0, 0.2)',
                        shadowBlur: 10
                        }
                    },
                    shape: 'circle',
                    indicator: [
                    { name: '发挥性有机物', max: 5 },
                    { name: 'NOX排放量', max: 5 },
                    { name: '其他', max: 5 },
                    { name: '颗粒物', max: 5 }
                    ]
                },
                series: [
                    {
                        name: 'Budget vs spending',
                        type: 'radar',
                        
                        data: []
                    }
                ]
            },
        }
    },
    watch: {
        newOption(val) {
            this.dataArr = val;
            this.initChart();
        },
    },
    mounted() {
        this.dataArr = this.newOption;
        this.initChart();
    },
    methods: {
        initChart() {
            // 基于准备好的dom，初始化echarts实例
            this.chart = echarts.init(this.$refs.storeEcharts);
            this.chart.setOption(this.myOption);
            this.chart.setOption({
                series: [{data: this.dataArr}],
            });
        },
    }
}
</script>